          float: left;
          text-align: left;
          padding: 0.12rem 0;
          img {
            width: 0.3rem;
            height: 0.3rem;
          }
        }
        .way_text {
          width: 3.17rem;
          height: 0.54rem;
          float: left;
          padding: 0 0.2rem 0 0.05rem;
          span {
            font-size: 0.14rem;
