          }
          .radio {
            float: right;
          }
        }
      }
      .way_weixin {
        width: 100%;
        height: 0.54rem;
        .way_weixin_text {
          border-bottom: 1px solid #f1f1f1;
        }
      }
      .way_zhifubao {
        width: 100%;
        height: 0.54rem;
      }
    }
  }
  .payOrder_hint {
    line-height: 0.14rem;
